.site-notification {
  position: fixed;
  top: 50%;
  left: -50%;
  width: 300px;
  z-index: 10;
  @include transform(translateY(-50%));
  @include transition(all 0.8s ease-in-out);


  &.active {
    left: 30px;
  }
  .item {
    overflow: hidden;
    padding: 10px;
    background: rgba(0,0,0,0.5);
    border-radius: 40px;
    @include box-shadow(0 8px 50px rgba(64,64,64,.05));

    .item-thumbnail {
      width: 50px;
      @include flex(0 0 auto);

      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        overflow: hidden;
      }
    }

    .item-content {
      padding-left: 15px;

      .item-title {
        color: #ffffff;
        font-size: 14px;
        margin-bottom: 5px;
        line-height: 1.2em;
      }
      p {
        color: #ffffff;
        font-size: 12px;
        line-height: 1.5em;
        margin-bottom: 5px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}